.container {
    @include am();
    padding: 0 em(15px);
}

@include mq(small) {
    .container {
        max-width: 750px;
    }
}

@include mq(medium) {
    .container {
        max-width: 970px;
    }
}

@include mq(large) {
    .container {
        max-width: 1200px;
    }
}